import store from "../../store";
import {updateSiteModelChecklist} from "../../actions/cbChecklist_Actions";

const addChecklist = () => {
  let allChecklists = store.getState().siteModelChecklist?.allChecklists ? store.getState().siteModelChecklist.allChecklists : [];
  allChecklists.push(({active:1,name:"",description:""}));
        store.dispatch(updateSiteModelChecklist({allChecklists:allChecklists}));
};

const NewChecklistButton = () => {
    return <button onClick={()=>addChecklist()}>New Checklist</button>
}

export default NewChecklistButton;